.sidebar{
    position: sticky;
    top: 80px;
    flex: 0.2;
    border-radius: 10px;
    text-align: center;
    height: fit-content;

}

.sidebar__avatar{
    margin-bottom: 10px;
}

.sidebar__top{
    display: flex;
    padding: 0.1px;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightgray;
    border-bottom: 0;
    border: top left radius 10px;
    border-top-right-radius: 10px;
    background-color: white;
    padding-bottom: 5px;

}

.sidebar__top>img{
    margin-bottom: -20px;
    width: 100%;
    height: 60px;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    object-fit: cover;
}

.sidebar__stat{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;

}

.sidebar__stat >p{
     color: gray;
     font-size: 13px;
     font-weight: 600;    
}

.sidebar__statNumber{
    font-weight: bold;
    color:#0a66c2 !important ;
}

.sidebar__stats{
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.sidebar__top>h4{
    color: gray;
    font-size: 12px;  
}

.sidebar__top>h2{
  font-size: 10px; 
}

.sidebar__bottom{
    text-align: left;
    padding: 10px;
    border:1px solid lightgray;
    background-color: white;
    border-radius: 10px;
    margin-top: 10px;
}

.sidebar__bottom >div{
    font-size: 13px;
    display:flexbox;
    color: gray;
    font-weight: bolder;
    cursor: pointer;
    padding: 5px;
}
