.login{
    display: grid;
    place-items: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 80px;
    padding-bottom: 100px ;
}

.login>img{
    object-fit:contain;
    height: 110px;
    margin-top: 20px;
    margin-bottom: 20px;
    
}

.login>form{
    display: flex;
    flex-direction: column;
}

.login>form>input{
    width: 300px;
    height: 40px;
    font-size: 20px;
    padding-left: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
}

.login>form>button{
    width: 310px;
    height: 40px;
    font-size: large;
    color: #fff;
    background-color: #0074B1;
    border-radius: 5px;
}

.login__register{
    color:#0177b7;
    cursor:pointer;
}

.login>p{
    margin-top: 20px;
}